 import api from '../utils/api';
import axios from 'axios';
import { setAlert } from './alert';
import {
    REGISTER_SUCCESS,
    REGISTER_FAIL,
    USER_LOADED,
    AUTH_ERROR,
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    LOGOUT,
    CLEAR_PROFILE
} from './types';
import setAuthToken from '../utils/setAuthToken';

//LOAD USER
export const loadUser = () => async dispatch => {
    // we need to check token if it is , we will put into global header x-auth-token
    //if we had just token in our localStorage we just want to always send that (means alreday logged in user)

    if(localStorage.token) {
        setAuthToken(localStorage.token);
    }

    try {
        const res = await axios.get('api/auth');
        // const res = await api.get('/auth');

        dispatch({
          type: USER_LOADED,
          payload: res.data
        });
    } catch (err) {
        dispatch({
            type:AUTH_ERROR
        });
        
    }
}

//Register User
// export const register = ( formData) => async dispatch => {

export const register = ({ name, email, password}) => async dispatch => {
    const config = {
        headers: {
            'Content-Type': 'application/json'
        }
    }

    const body = JSON.stringify({name, email, password});
    try {
        const res = await axios.post('/api/users', body, config);
      
        dispatch({
          type: REGISTER_SUCCESS,
          payload: res.data
        });

        dispatch(loadUser());
      } catch (err) {
        err.response?.data?.errors?.forEach(
          error => dispatch(setAlert(error.msg, 'danger'))
        );
      
        dispatch({
          type: REGISTER_FAIL
        });
      }
};

//Login User
export const login = (email, password) => async dispatch => {
    const config = {
        headers: {
            'Content-Type': 'application/json'
        }
    }

    const body = JSON.stringify({email, password});
    try {
        const res = await axios.post('/api/auth', body, config);
      
        dispatch({
          type: LOGIN_SUCCESS,
          payload: res.data
        });

        dispatch(loadUser());
      } catch (err) {
        err.response?.data?.errors?.forEach(
          error => dispatch(setAlert(error.msg, 'danger'))
        );
      
        dispatch({
          type: LOGIN_FAIL
        });
      }
};

//Logout / Clear the Profile
export const logout = () => dispatch => {
  dispatch({type: CLEAR_PROFILE});
  dispatch({type: LOGOUT});
};
